import React from 'react'
import { clientIDGitHub, clientSecretGitHub } from '../utils/keys'

function accessToken(code) {

  fetch(`https://github.com/login/oauth/access_token/?client_id=${clientIDGitHub}&client_secret=${clientSecretGitHub}&code=${code}`, {
    method: 'POST',
    headers: {
        'Accept': 'application/json',
      },
  }).then(res => {
      console.log(res);
      fetch(`https://api.github.com/users/elfiservice`, { 
        headers: {
            'Authorization': 'token OAUTH-TOKEN',
          },
       })
      .then(res => {
          console.log(res.json());
           
        })
  })

}

const MyCompt = (props) => {
    console.log(props.urlData.location.search.split('='));
    const paransFromUrlCallback = props.urlData.location.search.split('=')
    const code = paransFromUrlCallback[1]
    accessToken(code)
    return (
        <div> Code Aqui {code} </div>
    )
}

export default MyCompt